/* Stuff added for calendar example */

/* FONT IMPORTS */

.icon {
    font-family: 'Material Icons', serif;
    font-style: normal;
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
  
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-feature-settings: 'liga';
            font-feature-settings: 'liga';
  }

/* VARIABLES */

:root {
    --main-color: #1a8fff;
    --text-color: #777;
    --text-color-light: #ccc;
    --border-color: #eee;
    --bg-color: #f9f9f9;
    --neutral-color: #fff;
  }
  
  
  /* GENERAL */
  
  * {
    box-sizing: border-box;
  }
  
  body {
      font-family: 'Nunito', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
      font-size: 1em;
      font-weight: 300;
      line-height: 1.5;
      color: #777;
      color: var(--text-color);
      position: relative;
  }

  button {
    font-family: 'Nunito', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  }
  
  header {
    display: block;
    width: 100%;
    padding: 1.75em 0;
    border-bottom: 1px solid #eee;
    border-bottom: 1px solid var(--border-color);
    background: #fff;
    background: var(--neutral-color);
  }
  
  header #logo {
    font-size: 175%;
    text-align: center;
    color: #1a8fff;
    color: var(--main-color);
    line-height: 1;
  }
  
  header #logo .icon {
    padding-right: .25em;
  }
  
  main {
    display: block;
    margin: 0 auto;
    margin-top: 5em;
    max-width: 50em;
  }
  
  
  /* GRID */
  
  .row {
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
  }
  
  .row-middle {
    -webkit-align-items: center;
            align-items: center;
  }
  
  .col {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-basis: 0;
            flex-basis: 0;
    max-width: 100%;
  }
  
  .col-start {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    text-align: left;
  }
  
  .col-center {
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
  }
  
  .col-end {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    text-align: right;
  }
  
  
  /* Calendar */
  
  .calendar {
    display: block;
    position: relative;
    width: 100%;
    background: #fff;
    background: var(--neutral-color);
    border: 1px solid #cccccc;
    border: 1px solid var(--calendar-border-color);
  }
  
  .calendar .header {
    font-weight: 700;
    font-size: 115%;
    padding: 1em 0;
    border-bottom: 1px solid #cccccc;
    border-bottom: 1px solid var(--calendar-border-color);
  }
  
  .calendar .header .icon {
    cursor: pointer;
    transition: .15s ease-out;
  }
  
  .calendar .header .icon:hover {
    -webkit-transform: scale(1.75);
            transform: scale(1.75);
    transition: .25s ease-out;
    color: #1a8fff;
    color: var(--main-color);
  }
  
  .calendar .header .icon:first-of-type {
    margin-left: 1em;
  }
  
  .calendar .header .icon:last-of-type {
    margin-right: 1em;
  }
  
  .calendar .days {
    text-transform: uppercase;
    font-weight: 400;
    color: #777;
    color: var(--text-color);
    font-size: 80%;
    padding: .75em 0;
    border-bottom: 1px solid #cccccc;
    border-bottom: 1px solid var(--calendar-border-color);
  }

  .calendar .body .cell .badge {
    padding: .15em .3em;
    margin: .15em;
  }
  
  .calendar .days .col {
    padding-left: 5px;
    padding-right: 5px;
  }
  
  .calendar .body .cell {
    position: relative;
    height: 6em;
    border-right: 1px solid #cccccc;
    border-right: 1px solid var(--calendar-border-color);
    overflow: hidden;
    cursor: pointer;
    background: #fff;
    background: var(--neutral-color);
    transition: 0.25s ease-out;
    padding: 3px;
  }
  
  .calendar .body .cell .cell-content {
    margin-right: 20px;
  }
  
  .calendar .body .cell:hover {
    background: #f9f9f9;
    background: var(--bg-color);
    transition: 0.5s ease-out;
  }
  
  .calendar .body .selected {
    border-left: 10px solid transparent;
    border-top: 1px solid transparent;
    border-right: 1px solid transparent;
    border-bottom: 1px solid transparent;
    border-color: #1d5454; /* linear-gradient(45deg, #1d5454 0%,#53cbf1 40%);*/
    border-image-slice: 1;
  }
  
  .calendar .body .row {
    border-bottom: 1px solid #cccccc;
    border-bottom: 1px solid var(--calendar-border-color);
  }
  
  .calendar .body .row:last-child {
    border-bottom: none;
  }
  
  .calendar .body .cell:last-child {
    border-right: none;
  }
  
  .calendar .body .cell .number {
    position: absolute;
    font-size: 82.5%;
    line-height: 1;
    top: .2em;
    right: .2em;
    font-weight: 700;
    background-color: #eeeeee;
    height: 1.9em;
    width: 1.9em;
    text-align: center;
    vertical-align: middle;
    border-radius: 50%;
    border-style: solid;
    border-width: 1px;
    border-color: #dddddd;
    padding-top: 0.5em;
  }
  
  .calendar .body .disabled {
    opacity: 0.5;
  }
  
  .calendar .body .cell .bg {
    font-weight: 700;
    line-height: 1;
    color: #1a8fff;
    color: var(--main-color);
    opacity: 0;
    font-size: 8em;
    position: absolute;
    top: -.1em;
    right: -.05em;
    transition: .25s ease-out;
    letter-spacing: -.07em;
  }
  
  .calendar .body .cell:hover .bg, .calendar .body .selected .bg  {
    opacity: 0.05;
    transition: .5s ease-in;
  }
  
  .calendar .body .col {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-basis: calc(100%/7);
            flex-basis: calc(100%/7);
    width: calc(100%/7);
  }
  
  .calendar .body .col {
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-basis: calc(100%/7);
            flex-basis: calc(100%/7);
    width: calc(100%/7);
  }

  form .error {
      color: #ef6461;
  }

  :root {
    --calendar-border-color: #cccccc;
  }

  .noscroll { 
    overflow: hidden;
